import React from 'react'
import {ListItem, Icon} from 'react-onsenui' 

export default class AccordianElem extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      isOpen : false
    }
    this.accordianContents = this.accordianContents.bind(this);
  }

  accordianContents()
  {
    return (
      <div className="accordian-contents">
          {this.props.children}
      </div>
      )
  }

  render() {

    let contents = this.state.isOpen ? this.accordianContents() : '';

    return (
      <div className="accordian">
        <ListItem
          tappable={true}
          onClick={() => this.setState({isOpen : !this.state.isOpen})} >
          <div className="left">{this.props.title}</div>
          <div className="right">
            <Icon icon={this.state.isOpen ? "md-chevron-up" : "md-chevron-down"} />
          </div>
        </ListItem>
        {contents}
      </div>
    );

  }
}
