class WebSocketController {
  constructor() {
    this.msgCallbacks = {}; // used to store callback functions for msg
    this.currCallbackId = 0;

    this.sendMessage = this.sendMessage.bind(this);
    this.sendRequest = this.sendRequest.bind(this);
    this.addCallback = this.addCallback.bind(this);
    this.handleMessage = this.handleMessage.bind(this);
    this.sendFieldUpdate = this.sendFieldUpdate.bind(this);
    this.sendSave = this.sendSave.bind(this);
  

  }

  handleMessage(msg){
    let msgData = JSON.parse(msg.data);
    if(msgData.id !== undefined && this.msgCallbacks[msgData.id] !== undefined){
      this.msgCallbacks[msgData.id](msgData.data, true);
    }
  }

  addCallback(callbackFunc){
    let callbackId = this.currCallbackId;
    this.msgCallbacks[callbackId] = callbackFunc;
    this.currCallbackId++;
    return callbackId;
  }


  tryConnect(ip, onSuccess, onError, onClose){

    let defaultPath = "";
    let fullPath = "ws://" + ip + defaultPath;

    this.socket = new WebSocket(fullPath);
    this.socket.onopen = onSuccess;
    this.socket.onerror = onError;
    this.socket.onclose = onClose;
    this.socket.onmessage = this.handleMessage;
  }

  sendRequest(path, onResponse){
    let id = 0;
    if(onResponse !== undefined)
    {
      id = this.addCallback(onResponse)
    }
    if(this.socket !== undefined){
     this.socket.send(JSON.stringify({id : id, path : path}));
    }

  }

  sendFieldUpdate(pageName, objectPath, val){
      var sendObj = {
        path : "update",
        page : pageName, 
        objectPath : objectPath, 
        value : val};
      if(this.socket !== undefined){
       this.socket.send(JSON.stringify(sendObj));
      }
  }

  sendSave(pageName){
    this.socket.send(JSON.stringify({
      path : "save",
      page : pageName
    }));
  }

  sendReset(pageName){
    this.socket.send(JSON.stringify({
      path : "reset",
      page : pageName
    }));
  }

  

  sendMessage(msg){
      if(this.socket !== undefined){
        this.socket.send(msg);
      }
  }

}



class SettingsPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page : props.page
    };
  }

  render() {
      
      return (
        <Page
          className={(this.props.active ? "active" : "inactive") + " margin-toolbar-top"}>
        {this.props.page.fields.map(field => {

         return (
          <Field 
            fieldData={field}
            handleFieldUpdate={this.props.handleFieldUpdate}
            enumDataSource={this.props.page.enums}
            updatePath={this.props.page.pageName}
            key={field.name}
             /> 
             )})}
        </Page>
      );

    }

}



class SideMenu extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      isOpen : true
    };
  }

  renderHeader()
  {
    return (
      <ListTitle>Pages</ListTitle>
    );
  }

  render() {
    
    // check if mobile
    return(
        <SplitterSide
          side="left"
          width={this.props.isMobile ? 200 : 250}
          collapse={this.props.isMobile ? true : false}
          isOpen={this.props.isOpen}
          swipeable={true}>
          <Page>
            <List
              dataSource={this.props.pages}
              renderHeader={this.renderHeader}
              renderRow={(page, idx) => {
                return(
                  <ListItem
                    tappable={true}
                    onClick={(e) => this.props.onClickListItem(idx)}
                    key={idx}
                  >{page.pageName}</ListItem>
                )}}
            />
          </Page>
          </SplitterSide>        

    );
  }
}


class MainContent extends React.Component{

  render() {

    let toolbarLeft = this.props.isMobile ? (
      <div className="left">
              <ToolbarButton 
                onClick={this.props.showSideMenu}>
                <Icon icon={{default: "md-menu"}} />
              </ToolbarButton>
            </div>) : "";

    let toolbarRight = this.props.isMobile ? (<div className="right">
              <ToolbarButton 
                onClick={this.props.showSideMenu}>
                <Icon icon={{default: "md-settings"}} />
              </ToolbarButton>
            </div>) : "";

    return(
      <Page className={this.props.className}>
          <Toolbar
            style={{position: "relative"}}>
            {toolbarLeft}
            <div className="center">{this.props.pages[this.props.activePageIdx].pageName}</div>
            {toolbarRight}
          </Toolbar>
            {this.props.pages.map((p, idx) => {
                return (
                  <SettingsPage 
                    page={p}
                    handleFieldUpdate={this.props.handleFieldUpdate}
                    active={idx===this.props.activePageIdx} 
                    key={p.pageName}
                    />);
              })}
        </Page>
    );
  }
}

class FetchDataPage extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      targetIp : this.props.targetIp
    };
    this.handleClick = this.handleClick.bind(this);
    this.enterIpWindow = this.enterIpWindow.bind(this);
  }

  handleClick(){
    this.props.onSubmit(this.state.targetIp);
  }

  getButton(){
    return (
      <Button 
        id="go-btn"
        className={this.props.isTryingConnect ? "btn-warning" : ""}
        onClick={this.handleClick}>
        {this.props.isTryingConnect ? (<ProgressCircular indeterminate />) : "GO!"}</Button>
    )
  }


  enterIpWindow() {
    return (
        <Page>
        <div className="center-flex">
          <Card id="splash-card">
            <h1>Unity Setting Manager</h1>
            {this.props.isError ? (<p className="error">Please check IP address and try again!</p>) 
            : (<p>Please enter the target PC's IP address:</p>)}
            <Input 
              placeholder="127.0.0.1" 
              modifier="underbar"
              value={this.state.targetIp !== undefined ? this.state.targetIp : ""}
              className={"full-width" + (this.props.isError ? " error" : "")}
              onChange={(event) => { 
                this.setState({targetIp: event.target.value})}}
              />
            <div>
              {this.getButton()}
            </div>
          </Card>
        </div>
      </Page>

    );
  }

  render() {
    return this.enterIpWindow();
  }
}

class LoadingSpinner extends React.Component{


  render(){

    let classNames = "loading-spinner flexbox-centered ";
    classNames += this.props.isActive ? "visable" : "hidden";

    return(
    <div className={classNames}>
      <ProgressCircular indeterminate className="blue" /> 
    </div>
  )};
}



class CustomBottomToolbar extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      isConfirm : false,
      isSave : false
    };
    this.getConfirmDialog = this.getConfirmDialog.bind(this);
  }

  handleCancel(){
    this.setState({
      isConfirm : false
    });
  }

 handleConfirm(){
  if(this.state.isSave){
    this.props.handleSave();
  }else{
    this.props.handleReset();
  }
  this.handleCancel();
 }

 getConfirmDialog(){
  return (
      <AlertDialog isOpen={this.state.isConfirm} onCancel={this.handleCancel.bind(this)} cancelable>
             <div className="alert-dialog-title">{this.state.isSave ? "Save?" : "Reset?"}</div>
             <div className="alert-dialog-content">
               Are you sure you want to {this.state.isSave ? "save?" : "reset?"} This cannot be undone.
             </div>
             <div className="alert-dialog-footer flexbox-centered">
               <AlertDialogButton onClick={this.handleConfirm.bind(this)} className="alert-dialog-button">
                 Ok
               </AlertDialogButton>
               <AlertDialogButton onClick={this.handleCancel.bind(this)} className="alert-dialog-button">
                 Cancel
               </AlertDialogButton>
             </div>
         </AlertDialog>
  );

 }

  render() {
    return (
      <BottomToolbar className="flexbox-centered">
            <ToolbarButton className="half-width flexbox-centered btn-bottom-toolbar">
              <Button className="full-width btn-warning"
               onClick={() => {
                  this.setState({
                    isConfirm : true,
                    isSave : false
                  })
                }} >
                Reset
              </Button>
            </ToolbarButton>
            <ToolbarButton className="half-width flexbox-centered btn-bottom-toolbar">
              <Button 
                onClick={() => {
                  this.setState({
                    isConfirm : true,
                    isSave : true
                  })
                }}
                className="full-width">
                Save
              </Button>
            </ToolbarButton>
            {this.getConfirmDialog()}
          </BottomToolbar>
    );
  }
}



class MainApp extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      isReady : false,
      isShowSideMenu : false,
      activePageIdx : 0,
      pages : testData,
      isLoading : false,
      isTryingConnect : false,
      isError : false
    };
    this.handleTryConnect = this.handleTryConnect.bind(this);
    this.renderAppView = this.renderAppView.bind(this);
    this.renderLoadView = this.renderLoadView.bind(this);
    this.handleSave = this.handleSave.bind(this);
    this.handleFieldUpdate = this.handleFieldUpdate.bind(this);
    this.handleDidLoad = this.handleDidLoad.bind(this);
    this.socket = new WebSocketController();
    this.onConnect = this.onConnect.bind(this);
    this.onError = this.onError.bind(this);
    this.onClose = this.onClose.bind(this);

  }

  handleDidLoad(data) {
    this.setState({
      isReady : true,
      isConnected : true,
      isShowSideMenu : false,
      activePageIdx : 0,
      isTryingConnect : false,
      isError : false,
      pages : data,
      isLoading : false
    })
  }

  handleFieldUpdate(objectPath, val){
      this.socket.sendFieldUpdate(
        this.state.pages[this.state.activePageIdx].pageName,
        objectPath, 
        val);
  }

  onConnect(){
    let newState = {
      isReady : false,
      isShowSideMenu : false,
      activePageIdx : 0,
      isTryingConnect : false,
      isError : false,
      isConnected : true,
      isLoading : true
    };
    this.setState(newState);

    // request data from websocket
    this.socket.sendRequest("getData", (data, isSuccess) => {
      this.handleDidLoad(data);
    });
  }

  onError(){
    let newState = Object.assign({}, this.state, 
    {
      isLoading : false,
      isConnected : false,
      isError : true,
      isTryingConnect : false
    });
    this.setState(newState);
  }

  onClose(){
    let newState = Object.assign({}, this.state, 
    {
      isLoading : false,
      isConnected : false,
      isError : true,
      isTryingConnect : false,
      isReady : false
    });
    this.setState(newState);
  }


  handleTryConnect(ip){
    if(ip === "test")
    {
      this.handleDidLoad(testData);
      return;
    }

    this.socket.tryConnect(ip,
      this.onConnect,
      this.onError,
      this.onClose)

    let newState = {
          isReady : false,
          isShowSideMenu : false,
          activePageIdx : 0,
          isTryingConnect : true,
          isError : false,
          isConnected : true,
          isLoading : false
        };
    this.setState(newState);
  }

  handleSave(){
    this.socket.sendSave(this.state.pages[this.state.activePageIdx].pageName);
  }

  handleReset(){
    this.socket.sendReset(this.state.pages[this.state.activePageIdx].pageName);
    this.onConnect();
  }

  renderAppView() {
    let isMobile = window.matchMedia("(max-width: 600px)").matches;

    return  (
      <Splitter>
        <SideMenu 
          isMobile={isMobile}
          pages={this.state.pages}
          isOpen={this.state.isShowSideMenu} 
          onClickListItem={(idx) => this.setState(
            {isShowSideMenu : false,
              activePageIdx : idx,
              isReady : this.state.isReady
            })}/>
        <SplitterContent>
          <MainContent
            isMobile={isMobile}
            className="content-main"
            pages={this.state.pages}
            activePageIdx={this.state.activePageIdx}
            showSideMenu={() => this.setState({
              isShowSideMenu : true,
              activePageIdx : this.state.activePageIdx,
              isReady : this.state.isReady
            })}
            handleFieldUpdate={this.handleFieldUpdate}
          />
          <CustomBottomToolbar
            handleSave={this.handleSave.bind(this)}
            handleReset={this.handleReset.bind(this)}
           />
        </SplitterContent>
      </Splitter>

    );
  }

  renderLoadView(){
    return (
      <FetchDataPage 
        onSubmit={this.handleTryConnect}
        isTryingConnect={this.state.isTryingConnect}
        isError={this.state.isError}
      />);
  }

  render() {

    let contents = this.state.isReady ? 
        this.renderAppView() : 
        this.renderLoadView();

    return (
      <div>
      <LoadingSpinner isActive={this.state.isLoading} />
      {contents}
      </div>
    );
  }
}


ReactDOM.render(
  <MainApp />,
  document.getElementById('root')
);
