import React from 'react';
import './index.css';
import {Range, List, ListHeader, 
  ListItem, Input, Button,
  Switch, Select} from 'react-onsenui';
import AccordianElem from "./accordian.js"



class BaseField extends React.Component{
    constructor(props) {
    super(props);
    this.state = {
      value : this.props.fieldData.value
    }
    this.updateFieldValue = this.updateFieldValue.bind(this);
  }

  updateFieldValue(newVal)
  {
    if(this.props.handleFieldUpdate!== undefined)
    {
      this.props.handleFieldUpdate(this.props.updatePath, newVal);
    }
    this.setState({value : newVal});
  }

  baseListRender(contents) {
    return (
        <List className="field">
          <ListHeader>{this.props.fieldData.name}</ListHeader>
          {contents}
        </List>
      );
  }


  render() {
    return (<div />);
  }
}

class NumberGroup extends React.Component{
  constructor(props) {
    super(props);
    this.handleRangeChange = this.handleRangeChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  handleRangeChange(val){
     let fieldVal = this.sliderValueToFieldValue(val);

    fieldVal = this.props.numberType===undefined ? parseFloat(fieldVal) : parseInt(fieldVal);
    this.props.handleValueChange(fieldVal);
  }

  handleInputChange(inputVal){
    let fieldVal = this.props.numberType===undefined ? parseFloat(inputVal) : parseInt(inputVal);
    if(fieldVal !== undefined){
      this.props.handleValueChange(fieldVal);
    }
  }

  sliderValueToFieldValue(sliderVal)
  {
    let min = 0;
    let max = 1;
    if (this.props.min !== undefined && this.props.max !== undefined){
      min = this.props.min;
      max = this.props.max;
    }

    let sliderNormalized = sliderVal / 100.0;
    return min * (1 - sliderNormalized) + max * sliderNormalized;

  }

  fieldValueToSliderValue(){

    let min = 0;
    let max = 1;
    if (this.props.min !== undefined && this.props.max !== undefined){
      min = this.props.min;
      max = this.props.max;
    }

    let finalVal = (this.props.value - min) / (max - min) * 100;
    return finalVal;
  }
   render() {

    return (
          <ListItem>
            <Input
              className="input-float"
              type="number"
              value={this.props.numberType===undefined ?
                parseFloat(this.props.value).toString() : parseInt(this.props.value).toString()}
              onChange={(event) => {this.handleInputChange(event.target.value)}} />
            <Range
              className="flex-grow"
              value={ this.fieldValueToSliderValue(this.props.value) }
              onChange={(event) =>{this.handleRangeChange(event.target.value)}}
              />
            </ListItem>
      );
  }
}

class NumberField extends BaseField{
  constructor(props) {
    super(props);
    this.state = {
      value : props.fieldData.value
    }
  }

  render() {
    let min = this.props.fieldData.min!==undefined ? this.props.fieldData.min : 0;
    let max =  this.props.fieldData.max!==undefined ? this.props.fieldData.max : this.props.numberType==="int" ? 100 : 1;
    let contents = (
      <NumberGroup
            value={this.state.value}
            handleValueChange={(val) => {
              let finalVal = this.props.numberType==="int" ? Math.floor(val) : val;
              this.updateFieldValue(finalVal);
             }}
            min={min}
            max={max}
            numberType={this.props.numberType}
            />);

    return super.baseListRender(contents);
    
  }
}

class VectorField extends BaseField{
  constructor(props){
    super(props);
    this.changeVectorValue = this.changeVectorValue.bind(this);
  }

  changeVectorValue(index, newVal)
  {
    let newState = this.state.value.slice();
    newState[index] = newVal;
    this.updateFieldValue(newState);
  }

  render() {
    let min = this.props.fieldData.min === undefined ? 0 : this.props.fieldData.min;
    let max = this.props.fieldData.max === undefined ? 1 : this.props.fieldData.max;

    let contents = (
     
          <div className="vector-inputs flex-grow">
            {this.props.fieldData.value.map((v, idx) => {
              return (
                 <NumberGroup
              value={this.state.value[idx]}
              handleValueChange={(val) => {this.changeVectorValue(idx, val);}}
              min={min}
              max={max}
              key={idx}
              />
              );
            })}
            </div>
      );

    return super.baseListRender(contents);
  }

}

class ColorField extends BaseField{

  changeColorValue(index, newVal)
  {
    let newState = this.state.value.slice();
    newState[index] = newVal;
    this.updateFieldValue(newState);
  }

  render() {
    let contents = (
        <div className="flexbox">
          <div className="color-box" style={{"backgroundColor": "rgba(" + 
                  this.state.value.map((v, idx) => v*(idx===3 ? 1 : 255)).join(",") + ")"}} />
          <div className="color-inputs flex-grow">
          {this.props.fieldData.value.map((v, idx) => {
            return (
               <NumberGroup
            value={this.state.value[idx]}
            handleValueChange={(val) => {this.changeColorValue(idx, val);}}
            min={0}
            max={1}
            key={idx}
            />
            );
          })}
            </div>
          </div>
      );

    return super.baseListRender(contents);
  }
}


class EnumField extends BaseField{
    
  render() {
    let options = this.props.enumData.map((name, idx) => {
            return (
              <option 
                value={name}
                key={name}>
                {name}
              </option>
              );
          });
    let contents = (
      <ListItem>
        <Select
          value={this.state.value}
          onChange={(event) => this.updateFieldValue(event.target.value)} >
          <option>Dummy Option (but in onsenui)</option>
          {options}
        </Select>
      </ListItem>

    );

    return super.baseListRender(contents);
  }
}


class BoolField extends BaseField{

  onChange() {
    let newState = !this.state.value;
    this.updateFieldValue(newState);
  }


  render() {

    let contents = (
        <ListItem>
          <Switch
            onChange={(event) => {
              this.onChange();
            }}
            checked={this.state.value} />
            <span style={{marginLeft: "1em"}}>{this.props.fieldData.name}</span>
          </ListItem> 
          );

    return super.baseListRender(contents);
  }

}

class StringField extends BaseField{

  onChange(e){
    let newState = e.target.value;
    this.updateFieldValue(newState);
  }

  render() {

    let contents = (
      <ListItem>
          <Input
            value={this.state.value}
            placeholder={this.state.value}
            onChange={(event) => {
              this.onChange(event);
            }} />
          </ListItem> 
          );

      return super.baseListRender(contents);
  }
}

class ActionField extends BaseField{


  render(){
    let contents = (
      <ListItem>
        <Button 
          className="btn-action"
          onClick={() => {this.props.handleFieldUpdate(this.props.updatePath, true);}} >
          {this.props.fieldData.name}
        </Button> 
      </ListItem>
    );

    return super.baseListRender(contents);
  }


}

class ClassField extends BaseField{

  render() {

    let isFlattened = this.props.fieldData.flatten !== undefined ? true : false;

    let fields = this.props.fieldData.value.map(field => {
              return (<Field 
                handleFieldUpdate={this.props.handleFieldUpdate}
                fieldData={field} 
                enumDataSource={this.props.enumDataSource}
                updatePath={this.props.updatePath}
                key={this.props.updatePath + field.name}
                />)
            });

    let accordianElem = (
          <AccordianElem title={this.props.fieldData.name}>
            {fields}
          </AccordianElem>
        );

      return isFlattened ? (
        <div>
          {fields}
          </div>
        ) :  accordianElem;
   }
}


class ArrayField extends React.Component{

  render() {



    let accordianElems = this.props.fieldData.value.map((fieldCollection, idx) => 
      {
        // base name is just array elem index, but will change if we find array key
        let arrayName = this.props.fieldData.name + "_" + idx;
        let updatePath = this.props.updatePath + "/" + idx.toString();
        let childElems = fieldCollection.map(field => {
            if(field.arrayKey!==undefined){
              arrayName = field.value;
            }
            return (<Field 
                handleFieldUpdate={this.props.handleFieldUpdate}
                fieldData={field} 
                enumDataSource={this.props.enumDataSource}
                updatePath={updatePath}
                key={updatePath + "/" + field.name}
                />)
            });

        return (
          <AccordianElem 
            title={arrayName}
            key={idx}>
            {childElems}
          </AccordianElem>
        );
      });

      return (
        <div>
          {accordianElems}
        </div>
       );
   }
}



export default class Field extends React.Component {

  render() {

    // check if hidden
    if(this.props.fieldData.hidden)
    {
      return "";
    }

    let fieldType = this.props.fieldData.type;
    let toReturn = null;

    switch (fieldType) {
      
      case 'float' :
        toReturn = <NumberField 
          handleFieldUpdate={this.props.handleFieldUpdate}
          key={this.props.fieldData.name} 
          fieldData={this.props.fieldData} 
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name}
          />
        break;

      case 'int' :
        toReturn = <NumberField 
          handleFieldUpdate={this.props.handleFieldUpdate}
          key={this.props.fieldData.name} 
          numberType="int" 
          fieldData={this.props.fieldData} 
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name}
          />
        break;

      case 'color' :
        toReturn = <ColorField
          handleFieldUpdate={this.props.handleFieldUpdate} 
          key={this.props.fieldData.name} 
          fieldData={this.props.fieldData}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name} />
        break;

      case 'bool':
        toReturn = <BoolField 
          handleFieldUpdate={this.props.handleFieldUpdate}
          key={this.props.fieldData.name} 
          fieldData={this.props.fieldData}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name} />
        break;

      case 'string':
        toReturn = <StringField 
          key={this.props.fieldData.name} 
          handleFieldUpdate={this.props.handleFieldUpdate}
          fieldData={this.props.fieldData}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name} />
        break;

      case 'action':
        toReturn = <ActionField
          key={this.props.fieldData.name} 
          handleFieldUpdate={this.props.handleFieldUpdate}
          fieldData={this.props.fieldData}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name} />
          break;

      case 'array':
        toReturn = <ArrayField 
          handleFieldUpdate={this.props.handleFieldUpdate}
          fieldData={this.props.fieldData} 
          enumDataSource={this.props.enumDataSource}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name}/>
        break;

      case 'vector':
        toReturn = <VectorField 
          key={this.props.fieldData.name} 
          handleFieldUpdate={this.props.handleFieldUpdate}
          fieldData={this.props.fieldData}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name} />
        break;

      case 'class':
          toReturn = <ClassField 
            fieldData={this.props.fieldData} 
            handleFieldUpdate={this.props.handleFieldUpdate}
            enumDataSource={this.props.enumDataSource}
            updatePath={this.props.updatePath + "/" + this.props.fieldData.name}
            />
          break;

       case 'enum':
        toReturn = <EnumField 
          handleFieldUpdate={this.props.handleFieldUpdate}
          enumData={this.props.enumDataSource[this.props.fieldData.enumName]}
          key={this.props.fieldData.name} 
          fieldData={this.props.fieldData}
          updatePath={this.props.updatePath + "/" + this.props.fieldData.name} />
        break;

      default: 
        break;
    }   

    return toReturn;
  }


}